#landing-section {
    background: url("../../../images/landing1.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

#landing-content-background {
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(200, 200, 200, 0.9), rgba(255, 255, 255, 0));
}

#landing-content-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    height: 100vh;
}

#logo {
    width: 30%;
}

#nav-items {
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    a {
        text-decoration: none !important;
        transition-property: color;
        transition-duration: 0.5s;

        color: rgb(55, 55, 55) !important;

        font-size: 1.5rem;

        &:visited {
            color: inherit;
        }

        &.selected {
            color: inherit;
        }

        &:hover {
            color: green !important;
        }
    }
}
